/*  Home-Page Module Mixins and Extends */
@import "modules/home-page/home-module";
@import "modules/projects-page/mixins";
/* Gobal Variables */
@import "partials/variables";
@import "partials/typography";
@import "vendor/bourbon";
// Media Q
@import "modules/global-mqs/mqs";

html{
    height:100%;
}

body{
    min-height:100%;
    background-color: whitesmoke;
}

header{
    padding-top: 30px;
    bottom:100px;
    top:30px;
}

.aloha{
  font-family:$monte;
  font-weight: 700;
}

.titleName{
    text-align: center;
}

.name, .title{
    color:$typeColor;
    font-weight: 400;
    font-family: $monte;
    @include ms-respond(font-size, 2);
}

.title{
    padding-top:-25px;
    padding-bottom: 25px;
    margin-top: -25px;
}

.intro-msg{
    // padding for left right and bottom links
    @include left-right-bottom-links;
    // left might margins pushed in closer
    @include left-right-margin;
    @include ms-respond(font-size, 2);
    text-align: center;
    line-height:ms(1);
}

.main{
  padding:1rem 0rem 1rem;
}

.built{
    @include left-right-bottom-links;
    // left might margins pushed in closer
    @include left-right-margin;
    text-align: center;
    @include ms-respond(font-size, -0);
}

header #projects:hover,
header #projects:focus {
   color:blue;
}


header #projects {
    color:$typeColor;
    padding: 2px;
    font-weight: 900;
    margin: 0 -2px;
}

header #projects{
    @include hover-links;
}

header #projects:hover{
   background-size:100% 100%;
}

.tools{
  display: flex;
  justify-content: center;
}

#skills-graphic{
  height:320px;
}

.graphic-text{
  font-weight: 900;
  font-family: $monte;
  color:$typeColorB;
  @include ms-respond(font-size,-1);
}


.skill{
  font-family: $monte;
  @include ms-respond(font-size,-3);
}
.under{
  transition:transform 0.3s;
}
.move{
  transition:transform 0.3s;
  transform:translateY(-35px);
}

// circle + triangle
.gC, .gT{
  transition:transform 0.3s;
  transform:translateY(0px);
}

// circle DOWN
.playGc{
  transition:transform 0.3s;
  transform:translateY(10px);
}
//triangle UP
.playGt{
  transition:transform 0.3s;
  transform:translateY(-10px);
}
// headerfooter
.wN, .wF{
  @include pWidth-desk;
}
// headerfooter responsive animation
// web nav web footer
.playwN, .playwF{
  @include pWidth-mobile;
}

// content
.p1, .img, .p2{
  @include pWidth-desk;
}

.playP1, .playImg, .playP2{
  @include pWidth-mobile;
}

.webD{
  transition:transform 0.5s;
  transform:translateX(0px);
}

.move-L{
  transition:transform 0.5s;
  transform:translateX(8px);
}
.play{
  transition:transform 0.5s;
  transform:translateX(0px);
}
.frame{
  transition:transform 0.5s;
  transform:skew(0deg, 0deg);
}
.skewFrame{
  transition:transform 0.5s;
  transform:skew(13deg, 0deg);
}

.movePlay{
  transition:transform 0.5s;
  transform:translateX(-34px);
}

.tools{
  text-align:center;
  @include ms-respond(font-size,1);
}

.under, .skill {
  @include tablet {
    visibility: hidden;
  }
}
